@import 'tailwindcss/base';
@import 'tailwindcss/components';
@import 'tailwindcss/utilities';

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.symplometro-app {
  min-width: 300px;
}

.symplometro-app .loading {
  height: 60px;
  padding: 10px;
  font-size: 1.6em;
  display: flex;
  justify-content: center;
  align-items: center;
}

.symplometro-app .box-events {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 10px;
  height: 60px;
}

.symplometro-app .box-events .actual-events {
  font-size: 1.6em;
}

.symplometro-app .box-events .top-events {
  font-size: 1em;
  color: #b2bec3;
  font-style: italic;
  font-weight: 300;
}

.symplometro-app .box-events .cute-text {
  font-size: 0.7em;
  font-weight: 600;
}

.symplometro-app .footer {
  text-align: center;
  background-color: #ecf0f1;
  color: #323232;
  padding: 5px;
  font-size: 1.2em;
}

.symplometro-app .footer a {
  text-decoration: none;
  color: #0a6ebd;
  font-weight: 600;
}

.symplometro-app .footer b {
  font-weight: 600;
}

.symplometro-app .notification {
  color: #fff;
  text-align: center;
}

.symplometro-app .notification > div:hover {
  cursor: pointer;
}

.symplometro-app .notification .on {
  background-color: #2ecc71;
  padding: 3px;
}

.symplometro-app .notification .off {
  background-color: #e74c3c;
  padding: 3px;
}

